.full-screen-close {
    position: fixed;
    top: 3%;
    right: 5%;
    z-index: 20;
}

.full-screen-close button {
    width: 50px;
    height: 50px;
    background: none;
    border: none;
    opacity: 50%;
    cursor: pointer;
}

.full-screen-close button:hover {
    opacity: 100%;
}

.close-icon line{
    stroke: white;
    stroke-width: 2px;
    stroke-linecap: round;
}
