.presentation-image {
    pointer-events: all;
}

.presentation-image--fade-in {
    animation: fadeIn 300ms;
}

.presentation-image--fade-out {
    animation: fadeOut 150ms;
}

@keyframes fadeIn {
    0% {opacity:0;}
    100% {opacity:1;}
}


@keyframes fadeOut {
    0% {opacity:1;}
    100% {opacity:0;}
}