.photo-container {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 100%;
    border: 6px solid white;
    background: aliceblue;
    overflow: hidden;
    transition: padding 100ms;
}

.photo-container.selected {
    padding: var(--padding-selected);
}

.photo-container.selected .photo {
    opacity: var(--opacity-selected);
}
