.layout-config {
    display: flex;
    padding: 1.2em 1em calc(1em - 8px);
}

.left-controls .form-label {
    margin-right: 1em;
}

.label-text {
    margin-right: .5em;
}

.right-controls {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    flex-grow: 1;
}

.right-controls .form-button {
    margin-left: 1em;
}