.file-thumb {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 180px;
    max-width: 240px;
    margin: .4em auto;
    background: var(--color-background);
    border: 2px solid transparent;
    transition: padding 100ms ease-in-out;
}

.file-thumb__image {
    width: 100%;
    height: 100%;
    border-radius: 0;
    object-fit: contain;
}

.file-thumb__icon {
    display: none;
    position: absolute;
    bottom: 10px;
    right: 10px;
    z-index: 2;
}

.file-thumb__icon.used {
    display: block;
}

.file-thumb.selected {
    border-color: var(--color-selected-border);
    padding: var(--padding-selected);
}


.file-thumb.selected .file-thumb__image {
    opacity: var(--opacity-selected);
}