.files-manager {
    position: fixed;
    display: grid;
    grid-template-columns: 250px auto;
    grid-template-rows: 50px var(--files-manager-height);
    max-height: 60vh;
    width: 100%;
    top: 100%;
    background: white;
    z-index: 10;
}

.files-manager.show {
    transform: translateY(calc(var(--files-manager-height) * -1));
}

