.row-layout {
    display: grid;
    margin: 0 auto;
}

.FULL-WIDTH .photo-container {
    width: 100%;
    height: 100%;
}

.C50_C50 {
    grid-template-columns: repeat(2, 1fr);
}

.C75_C25 {
    grid-template-columns: 75% 25%;
}

.C25_C75 {
    grid-template-columns: 25% 75%;
}


.C33_C33_C33 {
    grid-template-columns: repeat(3, 1fr);
}

/*-------------------------------------------------*/

.C60R2_C40 {
    grid-template-columns: 60% 40%;
    grid-template-rows: 50%;
}

.C60R2_C40 .element_0 {
    grid-row: 1 / 3;
}

/*-------------------------------------------------*/

.C40_C60R2 {
    grid-template-columns: 40% 60%;
    grid-template-rows: 50%;
}

.C40_C60R2 .element_2 {
    grid-column: 2 / 3;
    grid-row: 1 / 3;
}

/*-------------------------------------------------*/

.C25R2_C75 {
    grid-template-columns: 25% 75%;
    grid-template-rows: 50%;
}

.C25R2_C75 .element_2 {
    grid-column: 2 / 3;
    grid-row: 1 / 3;
}

/*-------------------------------------------------*/

.C75_C25R2 {
    grid-template-columns: 75% 25%;
    grid-template-rows: 50%;
}

.C75_C25R2 .element_0 {
    grid-row: 1 / 3;
}

/*-------------------------------------------------*/

.C25_C25_C25_C25 {
  grid-template-columns: repeat(4, 1fr);
}

/*-------------------------------------------------*/

.C25R3_C75 {
    grid-template-columns: 25% 75%;
    grid-template-rows: repeat(3, 1fr);
}

.C25R3_C75 .element_3 {
    grid-column: 2 / 3;
    grid-row: 1 / 4;
}

/*-------------------------------------------------*/

.C75_C25R3 {
    grid-template-columns: 75% 25%;
    grid-template-rows: repeat(3, 1fr);
}

.C75_C25R3 .element_0 {
    grid-column: 1 / 2;
    grid-row: 1 / 4;
}

/*-------------------------------------------------*/

.C33_C33_C33R2 {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);
}

.C33_C33_C33R2 .element_0, .C33_C33_C33R2 .element_1 {
    grid-row: 1 / 4;
}

/*-------------------------------------------------*/

.C33_C33R2_C33 {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);
}

.C33_C33R2_C33 .element_0, .C33_C33R2_C33 .element_3 {
    grid-row: 1 / 4;
}

.C33_C33R2_C33 .element_3 {
    grid-column: 3 / 4;
}

/*-------------------------------------------------*/

.C33R2_C33_C33 {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);
}

.C33R2_C33_C33 .element_2, .C33R2_C33_C33 .element_3 {
    grid-row: 1 / 4;
}

.C33R2_C33_C33 .element_2 {
    grid-column: 2 / 3;
}

.C33R2_C33_C33 .element_3 {
    grid-column: 3 / 4;
}

/*-------------------------------------------------*/

.C25R2_C25_C50 {
    grid-template-columns: 25% 25% 50%;
    grid-template-rows: repeat(2, 1fr);
}

.C25R2_C25_C50 .element_2, .C25R2_C25_C50 .element_3 {
    grid-row: 1 / 3;
}

.C25R2_C25_C50 .element_2 {
    grid-column: 2 / 3;
}

.C25R2_C25_C50 .element_3 {
    grid-column: 3 / 4;
}

/*-------------------------------------------------*/

.C25_C25R2_C50 {
    grid-template-columns: 25% 25% 50%;
    grid-template-rows: repeat(2, 1fr);
}

.C25_C25R2_C50 .element_0, .C25_C25R2_C50 .element_3 {
    grid-row: 1 / 3;
}

.C25_C25R2_C50 .element_3 {
    grid-column: 3 / 4;
}

/*-------------------------------------------------*/

.C25_C50_C25R2 {
    grid-template-columns: 25% 50% 25%;
    grid-template-rows: repeat(2, 1fr);
}

.C25_C50_C25R2 .element_0, .C25_C50_C25R2 .element_1 {
    grid-row: 1 / 3;
}

.C25_C50_C25R2 .element_1 {
    grid-column: 2 / 3;
}

/*-------------------------------------------------*/

.C50_C25_C25R2 {
    grid-template-columns: 50% 25% 25%;
    grid-template-rows: repeat(2, 1fr);
}

.C50_C25_C25R2 .element_0, .C50_C25_C25R2 .element_1 {
    grid-row: 1 / 3;
}

/*-------------------------------------------------*/

.C50_C25R2_C25 {
    grid-template-columns: 50% 25% 25%;
    grid-template-rows: repeat(2, 1fr);
}

.C50_C25R2_C25 .element_0, .C50_C25R2_C25 .element_3 {
    grid-row: 1 / 3;
}

.C50_C25R2_C25 .element_3 {
    grid-column: 3 / 4;
}
