.text-container {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: auto;
    padding: 1em;
}

.text-container.selected {
    border: 1px solid gainsboro;
}

.text-container__wrapper {
    width: 100%;
    text-align: center;
}
