.full-screen-navigation {
    position: fixed;
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;
    top: 48vh;
    left: 0;
    z-index: 15;
}

.full-screen-navigation button {
    background: none;
    border: none;
    opacity: 40%;
    cursor: pointer;
}

.full-screen-navigation button:hover {
    opacity: 100%;
}

.navigation-icon-prev,
.navigation-icon-next {
    width: 80px;
    height: 80px;
}

.navigation-icon-prev path,
.navigation-icon-next path {
    fill: white;
    stroke-width: 2px;
}

.navigation-icon-prev path:first-child,
.navigation-icon-next path:first-child {
    display: none;
}

.navigation-icon-prev {
    transform: rotate(180deg);
}
