.select-image-button {
    position: absolute;
    width: 100%;
    height: 100%;
    border: none;
    background: none;
    z-index: 1;
}

.select-image-button__icon {
    position: absolute;
    top: 10px;
    left: 10px;
    width: 14px;
    height: 14px;
    border-radius: 100%;
    border: none;
    background: var(--color-selected-background);
    opacity: var(--opacity-selected);
}

.select-image-button__icon.selected {
    outline: 2px solid var(--color-selected-border);
    width: 16px;
    height: 16px;
    background: var(--color-background);
    opacity: 100%
}