.edit-row-controls {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 100;
}

.center-controls {
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    position: relative;
    height: 100%;
    width: 100%;
    z-index: 1;
}

.floating-left-controls, .floating-right-controls {
    position: absolute;
    display: inline-block;
    top: 20px;
    z-index: 2;
}

.floating-left-controls {
    left: 20px;
}

.floating-right-controls {
    right: 20px;
}