.photo-config {
    display: flex;
    padding: 1.2em 1em calc(1em - 8px);
}

.left-controls, .right-controls, .form-label {
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.right-controls {
    flex-grow: 1;
}

.left-controls .form-label {
    margin-right: 1em;
}

.label-text {
    margin-right: .5em;
}

.right-controls .form-button {
    margin-left: 1em;
}

.image-selector > * {
    margin-right: 1em;
}

.form-info-box {
    padding: .2em;
    /*background: gainsboro;*/
    border-radius: 5px;
    color: lightslategray;
}